"use client"

import { useState, useRef, useEffect } from "react"
import { useRouter } from "next/navigation"
import Script from "next/script"

interface WufooQuoteFormProps {
  onSuccess?: () => void
  source?: string
}

export default function WufooQuoteForm({ onSuccess, source }: WufooQuoteFormProps) {
  const router = useRouter()
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  })
  const [file, setFile] = useState<File | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)
  const formRef = useRef<HTMLFormElement>(null)

  useEffect(() => {
    // Add CSS link
    const link = document.createElement('link')
    link.rel = 'stylesheet'
    link.href = '/css/wufoo-form.css'
    document.head.appendChild(link)
    
    // Initialize wufoo form behavior after component mounts
    if (typeof window !== 'undefined' && formRef.current) {
      // The wufoo.js script will handle form initialization
      const script = document.createElement('script')
      script.src = '/scripts/wufoo.js'
      script.async = true
      document.body.appendChild(script)
      
      return () => {
        // Cleanup
        const existingScript = document.querySelector('script[src="/scripts/wufoo.js"]')
        if (existingScript) {
          document.body.removeChild(existingScript)
        }
        const existingLink = document.querySelector('link[href="/css/wufoo-form.css"]')
        if (existingLink) {
          document.head.removeChild(existingLink)
        }
      }
    }
    
    return () => {
      const existingLink = document.querySelector('link[href="/css/wufoo-form.css"]')
      if (existingLink) {
        document.head.removeChild(existingLink)
      }
    }
  }, [])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setFile(e.target.files[0])
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    
    // Disable submit button (wufoo.js style)
    const submitButton = document.getElementById('saveForm') as HTMLButtonElement
    if (submitButton) {
      submitButton.disabled = true
    }
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    setIsSubmitting(false)
    // Reset form
    setFormData({ name: "", phone: "", email: "", message: "" })
    setFile(null)
    if (fileInputRef.current) {
      fileInputRef.current.value = ""
    }
    
    // Close modal if callback provided
    if (onSuccess) {
      onSuccess()
    }
    
    // Navigate to thank you page
    router.push("/thank-you")
  }

  return (
    <>
      <Script src="/scripts/wufoo.js" strategy="lazyOnload" />
      <div id="public" className="wufoo-form-container">
        <form 
          ref={formRef}
          onSubmit={handleSubmit} 
          className="wufoo"
          id="form"
          name="form"
          method="post"
        >
          <ul>
            <li id="foli1" className="notranslate">
              <label className="desc" htmlFor="Field1">
                Your Name
                <span className="req">*</span>
              </label>
              <div>
                <input
                  id="Field1"
                  name="Field1"
                  type="text"
                  className="field text medium"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                />
              </div>
            </li>

            <li id="foli2" className="notranslate">
              <label className="desc" htmlFor="Field2">
                Your Phone
              </label>
              <div>
                <input
                  id="Field2"
                  name="Field2"
                  type="tel"
                  className="field text medium"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                />
              </div>
            </li>

            <li id="foli3" className="notranslate">
              <label className="desc" htmlFor="Field3">
                Your Email
                <span className="req">*</span>
              </label>
              <div>
                <input
                  id="Field3"
                  name="Field3"
                  type="email"
                  className="field text medium"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  required
                />
              </div>
            </li>

            <li id="foli4" className="notranslate">
              <label className="desc" htmlFor="Field4">
                Your Message
                <span className="req">*</span>
              </label>
              <div>
                <textarea
                  id="Field4"
                  name="Field4"
                  className="field textarea medium"
                  rows={10}
                  value={formData.message}
                  onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                  required
                />
              </div>
            </li>

            <li id="foli5" className="notranslate">
              <label className="desc" htmlFor="Field5">
                Attach a File
              </label>
              <div>
                <input
                  ref={fileInputRef}
                  id="Field5"
                  name="Field5"
                  type="file"
                  className="field file"
                  onChange={handleFileChange}
                />
                <span className="file-info">
                  {file ? file.name : "未选择任何文件"}
                </span>
              </div>
            </li>

            <li className="buttons">
              <div>
                <input
                  id="saveForm"
                  name="saveForm"
                  className="btTxt submit"
                  type="submit"
                  value="Submit"
                  disabled={isSubmitting}
                />
              </div>
            </li>
          </ul>
        </form>
      </div>
    </>
  )
}

